export default{
	
	data(){
		return{
			theMid:'',
			moveLineOffset: 100/8,
			currentIndex: 0,
			taitou:'',
			shibiehao:'',
			address:'',
			openBank:'',
			email:'',
			
			secondSection:[
				{
					index:0,
					title:'收票人信息',
				},
				{
					index:1,
					title:'发票内容',
				},
				{
					index:2,
					title:'发票金额'
				},
				{
					index:3,
					title:'发票张数'
				}
				
			],
			tabList: [{
				id: 0,
				name: '电子普票'
			}, {
				id: 1,
				name: '专用发票'
			}],
			page:1,
			loadingText: '正在加载数据...',
		}
	},
	onReachBottom() {
	    this.page++;
	    this.loadingText='正在加载更多数据...';
	    this.getServicList();
	},
	onPullDownRefresh() {
		this.getServicList();
	},
	onLoad(eee) {
		
		var _this = this;
		uni.getStorage({
			key: 'mid',
			success: function(res) {
				console.log('getStoragegemid' + res.data);
				_this.theMid = res.data;
				// _this.getServicList();
			}
		});
		// this.getServicList();
	},
	methods:{
		clickItem(e,index){
			this.showNewsData=[];
			this.page=1;
			var _this = this;
			_this.currentIndex = e.id;
			_this.moveLineOffset=(e.id*50)+100/8;
			this.loadingText='正在加载数据...';
			_this.isDraft=_this.currentIndex==2;
			uni.startPullDownRefresh({
				
			})
		},
		
		async getServicList(){
			
			var serverUrl="Home/Message/getList";
			var data={
				mid:this.theMid,
				type:this.currentIndex,
				limit:10,
			    page:this.page
			}
			var res = await this.requestFromServer({
				url:serverUrl,
				data:data
			})
			if(this.page==1){
				this.showNewsData=[];
			}
			uni.stopPullDownRefresh();
			if(res.data){
				this.showNewsData=this.showNewsData.concat(res.data);
				if(res.data.length<10){
					this.loadingText='没有更多数据了';
				}else{
					this.loadingText='加载更多数据...';
				}
			}else{
				this.loadingText='没有更多数据了';
			}
			
		},
		needknow() {
			uni.navigateTo({
				url: './receiptKnow'
			});
		},
		clickEdit(){
		    uni.navigateBack({
		    	
		    })
		},
		clickOk(){
		    uni.navigateTo({
		    	url:'receiptConfirm'
		    })	
		},
		
		dateFormat:function(time) {
			return this.getDateSecond(time);
		},
	}
}
